/**************************************************************************/
/*****************************    Global    *******************************/
/**************************************************************************/

body {
	background-color: rgb(15, 17, 24);
	background-image: url(/static/media/hero.bd007aec.svg);
	color: rgb(236, 236, 236);
	overflow-x: hidden;
}

.loading {
	margin-top: 140px;
	padding-bottom: 400px;
	text-align: center;
}

.cursor_ptr {
	cursor: pointer;
}

.green_text {
	color: #6cc027;
	font-family: var(--Poppins);
}

a[disabled] {
	color: gray;
}

a[disabled]:hover {
	color: gray;
	-webkit-animation: none;
	        animation: none;
	cursor: not-allowed;
}

a:hover {
	color: #6cc027 !important;
}

/* Scroll bar width */
::-webkit-scrollbar {
	width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
	background: rgb(13, 13, 13);
}

/* Handle */
::-webkit-scrollbar-thumb {
	border-radius: 50px;
	background: rgb(83, 194, 75);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: rgb(83, 194, 75);
}

:root {
	--Poppins: "Poppins", sans-serif;
	--Roboto: "Roboto", sans-serif;
}

/**************************************************************************/
/**************************    Progress bar    ****************************/
/**************************************************************************/

.progress-container {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 3px;
	background: transparent;
}

@-webkit-keyframes pulsate {
	100% {
		/* Larger blur radius */
		box-shadow: 0 0 0.2px #fff, 0 0 1px #fff, 0 0 6px rgb(71, 175, 63), 0 0 14px rgb(71, 175, 63),
			0 0 18px rgb(71, 175, 63), 0 0 23px rgb(71, 175, 63);
	}

	0% {
		/* A slightly smaller blur radius */
		box-shadow: 0 0 0.1px #fff, 0 0 0.6px #fff, 0 0 4px rgb(71, 175, 63), 0 0 10px rgb(71, 175, 63),
			0 0 14px rgb(71, 175, 63), 0 0 17px rgb(71, 175, 63);
	}
}

@keyframes pulsate {
	100% {
		/* Larger blur radius */
		box-shadow: 0 0 0.2px #fff, 0 0 1px #fff, 0 0 6px rgb(71, 175, 63), 0 0 14px rgb(71, 175, 63),
			0 0 18px rgb(71, 175, 63), 0 0 23px rgb(71, 175, 63);
	}

	0% {
		/* A slightly smaller blur radius */
		box-shadow: 0 0 0.1px #fff, 0 0 0.6px #fff, 0 0 4px rgb(71, 175, 63), 0 0 10px rgb(71, 175, 63),
			0 0 14px rgb(71, 175, 63), 0 0 17px rgb(71, 175, 63);
	}
}

/* The progress bar (scroll indicator) */
.progress-bar {
	height: 3px;
	background: rgb(77, 182, 70);
	width: 0%;
	-webkit-animation: pulsate 0.5s ease-in-out infinite alternate;
	        animation: pulsate 0.5s ease-in-out infinite alternate;
}

